/*Formik  React  React 
Formik React  React  */
import {Formik, Form} from "formik";
import { Link } from "react-router-dom";
import LoginInput from "../../components/inputs/loginInput";
import { useState } from "react";
//Yup  JavaScript  Yup
import * as Yup from "yup"; 
import DotLoader from "react-spinners/DotLoader";
import axios from "axios";
import Cookies from "js-cookie";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

const loginInfos = {
    email: "",
    password: "",
};

export default function LoginForm({setVisible}){
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [login, setLogin] = useState(loginInfos);
    const { email, password } = login;
    const handleLoginChange = (event) => {
        // target  name  value name  value 
        const {name, value} = event.target;
        // (...)  user  name  (value)
        setLogin({ ...login, [name]: value });
    };

    //
    const loginValidation = Yup.object({
        email: Yup.string().required("Email address is required.").email("Must be a valid email.").max(100),
        password: Yup.string().required("Password is required"),
    });

    const [error, setError] = useState("");
    const [loading, setLoading] = useState(false);
    const loginSubmit = async() => {
        try {
            setLoading(true);
            const {data} = await axios.post(`${process.env.REACT_APP_BACKEND_URL}/login`, {email, password});
            dispatch({type: "LOGIN", payload: data});
            Cookies.set("user", JSON.stringify(data));
            navigate("/");
        }catch(error){
            setLoading(false);
            setError(error.response.data.message);
        }
    };

    return (
        <div className="login_wrap">
            <div className="login_1">
                <img src="../../icons/facebook.svg" alt="" />
                <span>This is my own practice Website!</span>
            </div>

            <div className="login_2">
                <div className="login_2_wrap">
                    <Formik 
                        enableReinitialize //enableReinitialize  Formik  true  Formik  initialValues 
                        initialValues={{email, password}} //initialValues  email  password 
                        validationSchema={loginValidation} //
                        onSubmit={() => {
                            loginSubmit();
                        }}
                    >
                        {(formik) =>(
                            <Form>
                                <LoginInput 
                                    type="text"
                                    name="email"
                                    placeholder="Email address or Phone number"
                                    onChange={handleLoginChange}
                                />
                                <LoginInput 
                                    type="password"
                                    name="password" 
                                    placeholder="Password"
                                    onChange={handleLoginChange}
                                    bottom //LoginInput
                                />
                                <button className="blue_btn" type="submit">Log In</button>
                            </Form>
                        )}
                    </Formik>
                    {/*  React  Link to  */}
                    <Link className="forgot_password" to="/reset">Forgotten password ?</Link>

                    <DotLoader color="#1876f2" loading={loading} size={30} />
                    {error && <div className="error_text">{error}</div>}

                    <div className="sign_splitter"></div>
                    <button className="blue_btn open_signup" onClick={() => setVisible(true)}>Create Account</button>
                </div>
                <Link to="/" className="sign_extra">
                    <b>Create a Page </b>for a celebrity, brand or business
                </Link>
            </div>
        </div>
    );
}